@charset "UTF-8";

@font-face {
  font-family: "Hue";
  src: url("../public/fonts/Hue-Medium.woff2") format("woff2"), url("../public/fonts/Hue-Medium.otf") format("opentype"); /* Safari, Android, iOS */
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

* {
  padding: 0;
  margin: 0;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-size: 15px;
  letter-spacing: 0.04em;
  color: #333;
  position: relative;
  box-sizing: border-box;
  outline: 0;
}

body {
  background-color: #eee;
}

#component-header {

  .hero {
    background-color: #666;
    background-image: url(../public/img/background.png);
    background-position: 50%;
    background-size: 60%;
    border-bottom: 2px solid #fefefe;
    height: 200px;
    width: 100%;
  }
  
  .icon {
    position: absolute;
    background-color: #ccc;
    background-image: url(../public/img/icon-profile.png);
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    border: 2px solid #fefefe;
    border-radius: 50%;
    height: 108px;
    width: 108px;
    left: calc(50% - 54px);
    bottom: -54px;
  }
  
  .title {
    margin: 0 auto;
    width: calc(100% - 32px);
    max-width: 1260px;
  
    h1 {
      font-size: 1.5rem;
      color: #68a;
      margin-top: 90px;
      font-weight: bold;
      font-feature-settings: "palt";
    }
    
    h2 {
      font-size: 1.0rem;
      color: #333;
      margin-top: 4px;
      font-weight: bold;
      font-feature-settings: "palt";
    }  
  }
}

#component-works {
  display: flex;
  flex-wrap: wrap;
  width: calc(100% - 8px);
  max-width: 1280px;
  margin: 0 auto;
  margin-top: 48px;

  section {
    background-color: #fefefe;
    border-radius: 2px;
    margin: 0 4px 16px 4px;
    width: 100%;
    
    @media screen and (min-width: 540px) {
      width: calc((100% / 2) - 8px);
    }

    @media screen and (min-width: 900px) {
      width: calc((100% / 3) - 8px);
    }

    @media screen and (min-width: 1280px) {
      width: calc((100% / 4) - 8px);
    }

    .imageWrapper {
      position: relative;
      width: 100%;
      border: 2px solid #fefefe;
      border-radius: 2px;

      &:before {
        content:"";
        display: block;
        padding-top: 52.5%;
      }

      .image {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        border-radius: 2px;
        background-color: #aaa;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
      }
    }

    .info {
      padding: 16px;

      h3 {
        font-size: 14px;
        line-height: 20px;
        height: 40px;
        margin-bottom: 8px;
      }

      p {
        font-size: 14px;
        height: 28px;

        a {
          font-size: 14px;
          color: #369;
        }
      }

      .date {
        font-size: 14px;
        color: #aaa;
      }
    }
  }
}


#component-switch {

  .switch-container {
    position: fixed;
    width: calc(100vw - 32px);
    max-width: 360px;
    bottom: 16px;
    left: 16px;
    background-color: #242a3c;
    display: flex;
    border: none;
    height: 42px;
    border-radius: 21px;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.3);
  
    @media screen and (min-width: (360px + (16px * 2) + 1)){
      left: calc(50vw - (360px / 2));
    }
  
    .switch-item {
      margin: 1px;
      color: #aaa;
      width: 50%;
      height: 40px;
      border-radius: 20px;
      font-size: 16px;
      text-align: center;
      line-height: 40px;
  
      &:hover {
        cursor: pointer;
        background-color: #474f62;
      }
  
      &.selected {
        background-color: #d72;
        color: #fefefe;
      }
    }
  }
}


#component-profile {
  margin: 0 auto;
  width: calc(100% - 16px);
  max-width: 720px;

  h3 {
    margin-top: 64px;
    width: 100%;
    font-weight: bold;
    font-size: 1rem;
    border-left: 4px solid #6ab;
    padding-left: 6px;
  }

  .profile {
    width: 100%;
    margin-top: 8px;
    background-color: #fefefe;
    border-radius: 2px;
    padding: 16px;
    line-height: 2rem;
    font-size: 0.9rem;
    color: #333;
  }  

  .activities {
    .activity {
      display: grid;
      grid-template-columns: 100px 1fr;

      margin-top: 16px;
      width: 100%;
      border-radius: 2px;
      overflow: hidden;
      background-color: #fefefe;

      .image {
        background-color: #f8f8f8;
        max-height: 120px;

        a {
          display: block;
          width: 100%;
          height: 100%;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 8px;

        .title {
          font-weight: bold;
        }

        .subtitle {
          font-size: 0.8rem;
          color: #666;
        }

        .media {
          margin-top: 8px;
          font-size: 0.7rem;
          color: #999;
        }
      }
    }
  }

  .copyright {
    margin-top: 64px;
    margin-bottom: 64px;
    text-align: center;
    font-size: 0.7rem;
    color: #aaa;
  }
}
